import { ValidationProvider } from "vee-validate";
import api from "~/services/api";
export default {
  layout: "index",
  head() {
    return {
      title: "صفحه تکمیل اطلاعات",
      // meta:[
      //    {
      //        hid:"description",
      //        name:"description",
      //        content:""
      //    }
      // ]
    };
  },
  data() {
    return {
      form: {
        fname: "",
      },
      store : null,
      order_id : 'eroxr'
    };
  },
  components: {
    ValidationProvider,
  },
  methods: {
    select_way_payment() {
      const data_user = {
        name: this.$refs.fname.value + " " + this.$refs.lname.value,
        email: this.$refs.email.value,
        address: {
          province: this.$refs.province.value,
          city: this.$refs.city.value,
          address: this.$refs.address.value,
        },
        phone: this.$refs.phone.value,
      };
      const items = JSON.parse(localStorage.getItem("cartItems"));
      const items_second = [];
      items.forEach((element) => {
        items_second.push({
          id: element.id,
          quantity: element.count,
        });
      });
      const items_end = {
        //store_id: this.$store.state.users.id
        store_id: this.store.id,
        products: items_second,
      };

      // this.$bvModal.show('modal-prevent-closing')
      /*this.$store.dispatch("payments/select_way_payment", {
        address : data_user.address,
        products :items_end.products,
        store_id : items_end.store_id
      });*/
        this.$refs.PayForm.submit()
      /* this.$axios.post('/order/create',{
           address : data_user.address,
           products :items_end.products,
           store_id : items_end.store_id
       }).then(res => {
           if (res.data.data.order_id){
               this.order_id = res.data.data.order_id
              this.$refs.PayForm.submit()
           }
       })*/
    },
    do_payment() {
      let getway = null;
      if (this.$refs.getway1.checked) {
        getway = 1;
      } else if (this.$refs.getway2.checked) {
        getway = 2;
      } else if (this.$refs.getway3.checked) {
        getway = 3;
      } else {
        getway = "null";
      }

      this.$store.dispatch("payment/do_payment", {
        getway_id: getway,
      });
    },
      getStore(){
        api.get(`store/${this.$route.params.store_slug}`)
          .then(res => {
             this.store = res.data.data
          })
      }
  },
  computed: {
    getways() {
      this.$store.state.payments.getways;
    },
  },
    mounted() {
      this.getStore()
    }
};
